body {
  font-family: Arial, sans-serif;
}

img {
  display: block;
  max-width: 100%;
  margin: 20px auto;
  background-color: white;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

div {
  text-align: center;
}

input[type="number"] {
  padding: 5px;
  margin: 0 10px;
}

button {
  padding: 5px 10px;
  margin: 0 5px;
  cursor: pointer;
}

form {
  margin: 20px 0;
}
